@font-face {
    font-family: 'HK Grotesk';
    src: url('dist/fonts/hk-grotesk/HKGrotesk-Medium.eot');
    src: url('dist/fonts/hk-grotesk/HKGrotesk-Medium.eot?#iefix') format('embedded-opentype'),
        url('dist/fonts/hk-grotesk/HKGrotesk-Medium.woff2') format('woff2'),
        url('dist/fonts/hk-grotesk/HKGrotesk-Medium.woff') format('woff'),
        url('dist/fonts/hk-grotesk/HKGrotesk-Medium.ttf') format('truetype'),
        url('dist/fonts/hk-grotesk/HKGrotesk-Medium.svg#HKGrotesk-Medium') format('svg');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

